<template>
  <section class="community-section">
    <div class="meme-wall">
      <TransitionGroup name="fade">
        <div v-for="image in visibleImages" :key="image.id" 
             class="meme-container"
             :style="{ 
               left: `${image.x}%`, 
               top: `${image.y}%`
             }">
          <div class="meme-wrapper">
            <img :src="`https://solanabitcat.com/data/meme${image.id}.webp`" 
                 :alt="`meme${image.id}`" 
                 class="meme-img">
          </div>
        </div>
      </TransitionGroup>
      <div class="meme-overlay"></div>
    </div>

    <h2>Join Us</h2>
    
    <SocialLinksSection :isAccent="false" />
  </section>
</template>

<script setup>
import { ref, onMounted, onBeforeUnmount } from 'vue'
import SocialLinksSection from '@/components/pages/home/SocialLinksSection.vue'

const visibleImages = ref([])
let imageInterval = null

const MIN_IMAGES = 3
const MAX_IMAGES = 4
const CHANGE_INTERVAL = 2000
const MIN_DISTANCE = 30 // минимальное расстояние между картинками в процентах

const getRandomPosition = () => {
  const margin = 25 // Отступ от края в процентах
  let attempts = 0
  const maxAttempts = 10
  
  while (attempts < maxAttempts) {
    const position = {
      x: margin + Math.random() * (100 - 2 * margin),
      y: margin + Math.random() * (100 - 2 * margin),
      rotation: Math.random() * 20 - 10
    }
    
    // Проверяем расстояние до всех существующих картинок
    const isFarEnough = visibleImages.value.every(img => 
      getDistance(position, img) >= MIN_DISTANCE
    )
    
    if (isFarEnough || attempts === maxAttempts - 1) {
      return position
    }
    
    attempts++
  }
  
  // Если не удалось найти подходящую позицию
  return {
    x: margin + Math.random() * (100 - 2 * margin),
    y: margin + Math.random() * (100 - 2 * margin),
    rotation: Math.random() * 20 - 10
  }
}

const getDistance = (pos1, pos2) => {
  const dx = pos1.x - pos2.x
  const dy = pos1.y - pos2.y
  return Math.sqrt(dx * dx + dy * dy)
}

const addImage = () => {
  if (visibleImages.value.length >= MAX_IMAGES) return false
  
  const usedImages = visibleImages.value.map(img => img.id)
  const availableImages = Array.from({length: 9}, (_, i) => i + 1)
    .filter(num => !usedImages.includes(num))
  
  if (availableImages.length === 0) return false
  
  const randomImage = availableImages[Math.floor(Math.random() * availableImages.length)]
  let position = getRandomPosition()
  
  visibleImages.value.push({
    id: randomImage,
    ...position
  })
  
  return true
}

const removeImage = () => {
  if (visibleImages.value.length <= MIN_IMAGES) return false
  visibleImages.value.shift()
  return true
}

const shuffleImages = () => {
  const currentCount = visibleImages.value.length
  
  if (currentCount < MIN_IMAGES) {
    // Если картинок меньше минимума, добавляем
    addImage()
  } else if (currentCount > MAX_IMAGES) {
    // Если картинок больше максимума, удаляем
    removeImage()
  } else {
    // Если количество в допустимом диапазоне
    // С вероятностью 50% либо добавляем новую и удаляем старую,
    // либо оставляем как есть
    if (Math.random() > 0.5) {
      if (addImage()) {
        // Если добавили новую, удаляем старую
        removeImage()
      }
    }
  }
}

onMounted(() => {
  // При старте добавляем минимальное количество картинок
  for (let i = 0; i < MIN_IMAGES; i++) {
    addImage()
  }
  imageInterval = setInterval(shuffleImages, CHANGE_INTERVAL)
})

onBeforeUnmount(() => {
  if (imageInterval) {
    clearInterval(imageInterval)
  }
})
</script>

